<template>
    <div id="app">
        <header>
            <h1>My music</h1>
        </header>
        <main>
            <section class="player">
                <h2 class="song-title">{{current.title}} - <span>{{current.artist}}</span></h2>
                <div class="controls">
                    <button class="prev" @click="prev">Prev</button>
                    <button class="play" v-if="!isPlaying" @click="play">Play</button>
                    <button class="pause" v-else @click="pause">Pause</button>
                    <button class="next" @click="next">Next</button>
                </div>
            </section>
            <section class="playlist">
                <h3>The Playlist</h3>
                <button v-for="song in songs" :key="song.src" @click="play(song)" :class="(song.src == current.src) ? 'song playing' : 'song'">{{song.title}} - {{song.artist}}</button>
            </section>
        </main>
        <footer>
            <h6>Sergey Rudenko</h6>
        </footer>
    </div>
</template>

<script>
    export default {
        name: 'App',
        data() {
            return {
                current: {},
                index: 0,
                isPlaying: false,
                songs: [
                    {
                        title: '8-Bit Dreamscape',
                        artist: 'The Whole Other',
                        src: require('./assets/audio/8_bit_dreamscape.aac')
                    },
                    {
                        title: 'Amazement',
                        artist: 'Freedom Trail Studio',
                        src: require('./assets/audio/amazement.aac')
                    },
                    {
                        title: 'Ammo',
                        artist: 'Density & Time',
                        src: require('./assets/audio/ammo.aac')
                    },
                    {
                        title: 'Ant Fire',
                        artist: 'Rachel K Collier',
                        src: require('./assets/audio/ant_fire.aac')
                    },
                    {
                        title: 'Castleshire',
                        artist: 'Chris Haugen',
                        src: require('./assets/audio/castleshire.aac')
                    },
                    {
                        title: 'City of Jewels',
                        artist: 'Density & Time',
                        src: require('./assets/audio/city_of_jewels.aac')
                    },
                    {
                        title: 'Clover 3',
                        artist: 'Vibe Mountain',
                        src: require('./assets/audio/clover_3.aac')
                    },
                    {
                        title: 'Happy Sixth',
                        artist: 'Freedom Trail Studio',
                        src: require('./assets/audio/happy_sixth.aac')
                    },
                    {
                        title: 'Hear the Noise',
                        artist: 'John Deley and the 41 Players',
                        src: require('./assets/audio/hear_the_noise.aac')
                    },
                    {
                        title: 'Invisible Beauty',
                        artist: 'Aakash Gandhi',
                        src: require('./assets/audio/invisible_beauty.aac')
                    },
                    {
                        title: 'Juno In The Space Maze',
                        artist: 'Loopop',
                        src: require('./assets/audio/juno_in_the_space_maze.aac')
                    },
                    {
                        title: 'Luck Witch',
                        artist: 'Audio Hertz',
                        src: require('./assets/audio/luck_witch.aac')
                    },
                    {
                        title: 'Mr. Tea',
                        artist: 'Rondo Brothers',
                        src: require('./assets/audio/mr_tea.aac')
                    },
                    {
                        title: 'Parkside',
                        artist: 'Dan Lebowitz',
                        src: require('./assets/audio/parkside.aac')
                    },
                    {
                        title: 'Pila Pala Paradise',
                        artist: 'Rachel K Collier',
                        src: require('./assets/audio/pila_pala_paradise.aac')
                    },
                    {
                        title: 'Plantation Island',
                        artist: 'Stayloose',
                        src: require('./assets/audio/plantation_island.aac')
                    },
                    {
                        title: 'Slow Times Over Here',
                        artist: 'Midnight North',
                        src: require('./assets/audio/slow_times_over_here.aac')
                    },
                    {
                        title: 'Stranger Danger',
                        artist: 'Francis Preve',
                        src: require('./assets/audio/stranger_danger.aac')
                    },
                    {
                        title: 'Sun Machine One',
                        artist: 'Loopop',
                        src: require('./assets/audio/sun_machine_one.aac')
                    },
                    {
                        title: 'Take Me to the Depths',
                        artist: 'Midnight North',
                        src: require('./assets/audio/take_me_to_the_depths.aac')
                    },
                    {
                        title: 'Winds of Spring',
                        artist: 'The 126ers',
                        src: require('./assets/audio/winds_of_spring.aac')
                    }
                ],
                player: new Audio()
            }
        },
        methods: {
            play(song) {
                if (typeof song.src != "undefined") {
                    this.current = song;
                    this.player.src = this.current.src;
                }
                this.player.play();
                this.player.addEventListener('ended', function () {
                    this.index++;
                    if (this.index > this.songs.length - 1) {
                        this.index = 0;
                    }
                    this.current = this.songs[this.index];
                    this.play(this.current);
                }.bind(this));
                this.isPlaying = true;
            },
            pause() {
                this.player.pause();
                this.isPlaying = false;
            },
            next() {
                this.index++;
                if (this.index > this.songs.length - 1) {
                    this.index = 0;
                }
                this.current = this.songs[this.index];
                this.play(this.current);
            },
            prev() {
                this.index--;
                if (this.index < 0) {
                    this.index = this.songs.length - 1;
                }
                this.current = this.songs[this.index];
                this.play(this.current);
            }
        },
        created() {
            this.current = this.songs[this.index];
            this.player.src = this.current.src;
        }
    }
</script>

